#towerSelection {
  max-height: 15vh;
  background-color: #0000004d;
  flex-direction: row;
  font-family: Kanit, sans-serif;
  display: flex;
  position: fixed;
  top: 85vh;
  left: 10vw;
}

#towerSelection .tower {
  width: auto;
  height: auto;
  max-height: 100%;
  max-width: 100%;
  border-style: solid;
  border-width: thin;
  border-radius: 6px;
  flex-direction: row;
  padding: 5px;
  font-size: 48px;
  display: flex;
  position: relative;
}

#towerSelection .tower.selected {
  background-color: #00000080;
  border-style: solid;
  border-radius: 6px;
  font-weight: bold;
}

#towerSelection .tower .towerHoverText {
  visibility: hidden;
  width: max-content;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #000;
  border-radius: 6px;
  padding: 5px;
  font-size: 16px;
  transition: opacity 1s ease-in-out;
  position: absolute;
  top: 0%;
  left: 25%;
  transform: translate(-25%, -100%);
}

#towerSelection .tower:hover .towerHoverText {
  visibility: visible;
  opacity: 1;
}

