  height: auto;
  max-height: 100%;
  max-width: 100%;
  margin: auto;
  display: block;
}

#towerSelection .tower .towerInfo {
  width: 100px;
  max-width: 100%;
  max-height: 33%;
  height: auto;
  flex-direction: column;
  display: flex;
}

#towerSelection .tower .towerInfo .cost {
  text-align: center;
  max-width: 100%;
  max-height: 100%;
  height: auto;
  flex-direction: row;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  position: relative;
}

#towerSelection .tower .towerInfo .cost .towerInfoIcon {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

#towerSelection .tower .towerInfo .cost .costText {
  text-shadow: -1px 1px #fff, 1px 1px #fff, 1px -1px #fff, -1px -1px #fff;
